'use client';

import React from 'react';
import Image from 'next/image';
import Link from 'next/link';
import { motion, useReducedMotion } from 'framer-motion';

import { Container } from '../container';
import { Button } from '../elements/button';

type ButtonTarget = '_self' | '_blank' | '_parent' | '_top' | string;

type CTAButton = {
  id?: number;
  text?: string | null;
  URL?: string | null;
  target?: ButtonTarget | null;
  variant?: string | null;
};

type Props = {
  heading: string;
  sub_heading?: string | null;
  CTAs?: CTAButton[] | null;
  cover?: any; // Strapi Media (v4/v5 shape)
  locale: string;
};

const STRAPI_BASE = (process.env.NEXT_PUBLIC_STRAPI_URL ?? '').replace(/\/$/, '');

function toAbsUrl(url?: string | null) {
  if (!url) return null;
  if (/^https?:\/\//i.test(url)) return url;
  if (!STRAPI_BASE) return url;
  return `${STRAPI_BASE}${url.startsWith('/') ? '' : '/'}${url}`;
}

function getMediaUrl(media: any): string | null {
  if (!media) return null;

  // v5-ish (direct)
  const direct = media?.url;
  if (typeof direct === 'string' && direct.length) return toAbsUrl(direct);

  // v4-ish (data.attributes)
  const attr = media?.data?.attributes;
  const base = attr?.url;
  if (typeof base === 'string' && base.length) return toAbsUrl(base);

  // formats fallback (if you want a resized image)
  const fmts = attr?.formats;
  const candidates = [
    fmts?.large?.url,
    fmts?.medium?.url,
    fmts?.small?.url,
    fmts?.thumbnail?.url,
  ].filter(Boolean);

  if (candidates.length) return toAbsUrl(candidates[0]);

  return null;
}

function joinLocaleHref(locale: string, url?: string | null) {
  const u = (url ?? '').trim();
  const path = u ? (u.startsWith('/') ? u : `/${u}`) : '/';
  return `/${locale}${path}`;
}

export const CTA = ({ heading, sub_heading, CTAs, cover, locale }: Props) => {
  const reduce = useReducedMotion();
  const coverUrl = getMediaUrl(cover);

  return (
    <div className="relative py-10 bg-charcoal rounded-xl mx-4">
      <Container className="flex flex-col md:flex-row justify-between items-center w-full px-6 md:px-8 gap-8">
        {/* LEFT */}
        <div className="flex flex-col w-full md:flex-1">
          <motion.h2
            initial={reduce ? false : { opacity: 0, y: 10 }}
            whileInView={reduce ? undefined : { opacity: 1, y: 0 }}
            viewport={{ once: true, amount: 0.4 }}
            transition={{ duration: 0.5, ease: [0.22, 1, 0.36, 1] }}
            className="text-lightblack text-xl text-center md:text-left md:text-3xl font-bold mx-auto md:mx-0 max-w-xl"
          >
            {heading}
          </motion.h2>

          {sub_heading ? (
            <p className="max-w-md mt-6 md:mt-8 text-center md:text-left text-sm md:text-base mx-auto md:mx-0 text-secondary">
              {sub_heading}
            </p>
          ) : null}

          {!!CTAs?.length && (
            <div className="mt-8 md:mt-10 flex flex-col gap-3 items-center md:items-start">
              {CTAs.map((cta, index) => {
                const href = joinLocaleHref(locale, cta.URL);
                const target = cta.target ?? undefined;
                const rel = target === '_blank' ? 'noreferrer noopener' : undefined;

                return (
                  <Button
                    as={Link}
                    key={cta.id ?? index}
                    href={href}
                    variant={cta.variant as any}
                    target={target as any}
                    rel={rel as any}
                    className="py-3 bg-muted border-none text-white w-full md:w-auto"
                  >
                    {cta.text}
                  </Button>
                );
              })}
            </div>
          )}
        </div>

        {/* RIGHT (cover) */}
        {coverUrl ? (
          <motion.div
            initial={reduce ? false : { opacity: 0, y: 12, scale: 0.985 }}
            whileInView={reduce ? undefined : { opacity: 1, y: 0, scale: 1 }}
            viewport={{ once: true, amount: 0.35 }}
            transition={{ duration: 0.55, ease: [0.22, 1, 0.36, 1] }}
            className="relative w-full md:w-[420px] h-[220px] md:h-[280px] rounded-2xl overflow-hidden border border-charcoal bg-charcoal"
          >
            <Image
              src={coverUrl}
              alt={heading || 'CTA cover'}
              fill
              sizes="(min-width: 768px) 420px, 100vw"
              className="object-cover"
              priority={false}
            />
            {/* finom pr√©mium overlay */}
            <div className="absolute inset-0 pointer-events-none bg-gradient-to-tr from-lightblack/20 via-transparent to-muted/20" />
          </motion.div>
        ) : (
          <div className="hidden md:block md:w-[420px]" />
        )}
      </Container>
    </div>
  );
};
